import React, { useState, useImperativeHandle } from 'react'
import { Button } from '@material-ui/core'

const Togglable = React.forwardRef((props, ref) => {
  const [visible, setVisible] = useState(false)

  const hideWhenVisible = { display: visible ? 'none' : '' }
  const showWhenVisible = { display: visible ? '' : 'none'}

  const toggleVisibility = () => {
    setVisible(!visible)
  } 

  useImperativeHandle(ref, () => {
    return {
      toggleVisibility
    }
  })

  return (
    <div>
      <div style={hideWhenVisible}>
        <Button color='primary' variant="contained" onClick={toggleVisibility} fullWidth>{props.buttonLabelShow}</Button>
      </div>
      <div style={showWhenVisible}>
         <Button color='secondary' variant="contained" onClick={toggleVisibility} fullWidth>{props.buttonLabelHide ? props.buttonLabelHide : 'Cancel'}</Button>
         {props.children}
      </div>
      </div>
    ) 
})

export default Togglable
